/*------------------------------------*\
		DEFAULT
\*------------------------------------*/

/**
 * Debug mode will visually highlight any potential markup/accessibility quirks
 * in the browser. Set to `true` or `false`.
 */
$debug-mode: false!default;

/**
 * BASE UNIT
 */

// Rem settings for rem unit > tools
$rem-baseline: 16px !default;
$rem-fallback: false !default;
$rem-px-only: false !default;

// Em setting for em unit > tools
$em-base: 16px !default;
$em-line-height: 24px !default;

/**
 * BASE FONTS
 */

// Base main Font-family
$base-font-family: 'lato', sans-serif!default;

// base headings fonts  			 		// base headings line-height
$h1-size: em(60px, $em-base)!default;		$h1-line-height: em(28px, $em-line-height)!default;
$h2-size: em(48px, $em-base)!default;		$h2-line-height: em(28px, $em-line-height)!default;
$h3-size: em(44px, $em-base)!default;		$h3-line-height: em(28px, $em-line-height)!default;
$h4-size: em(38px, $em-base)!default;		$h4-line-height: em(28px, $em-line-height)!default;
$h5-size: em(34px, $em-base)!default;		$h5-line-height: em(30px, $em-line-height)!default;
$h6-size: em(30px, $em-base)!default;		$h6-line-height: em(32px, $em-line-height)!default;

// base paragaphs fonts 					// base paragaphs line-height
$p-font-size: em(15px,$em-base)!default;	$p-line-height: em(24px,$em-base)!default;

// base link fonts
$a-font-size: em(15px,$em-base)!default;


// base grid container width
$grid-width: 100% !default;

// base col
$c1: $grid-width / 1 !default;
$c2: $grid-width / 2 !default;
$c3: $grid-width / 3 !default;
$c4: $grid-width / 4 !default;
$c5: $grid-width / 5 !default;
$c6: $grid-width / 6 !default;
$c7: $grid-width / 7 !default;
$c8: $grid-width / 8 !default;
$c9: $grid-width / 9 !default;
$c10: $grid-width / 10 !default;
$c11: $grid-width / 11 !default;
$c12: $grid-width / 12 !default;

// base gutter
$gutter: 2.5em !default;

// base grid-system
$grid-system: 'flex'; //flex, float or use both as fallback inside the file


/**
 * BASE BREAKINGPOINTS
 */

$breakpoints: (
	'phone': 20em,
	'tablet': 48em,
	'desktop': 64em
) !default;
